import React from 'react';

//import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Row, Col } from 'reactstrap';

// react-bootstrap-table-next
import 'react-bootstrap-table-next/dist/react-bootstrap-table2.min.css';

import BootstrapTable from 'react-bootstrap-table-next';
import filterFactory, { selectFilter } from 'react-bootstrap-table2-filter';
import paginationFactory from 'react-bootstrap-table2-paginator';


import data from '../../data.json'

const statusOptions = {
	'': 'All',
	'Active': 'Active',
	'Expired': 'Expired',
	'Terminated': 'Terminated'
};


const columns = [{
	dataField: '',
	text: '',
	sort: false
},{
	dataField: 'police_number',
	text: 'Police Number',
	sort: true
}, {
	dataField: 'device_number',
	text: 'Device Number',
	sort: true
}, {
	dataField: 'service',
	text: 'Service',
	sort: true
}, {
	dataField: 'start_time',
	text: 'Start Time',
	sort: true
}, {
	dataField: 'end_time',
	text: 'End Time',
	sort: true
}, {
	dataField: 'update',
	text: 'Update',
	sort: true
}, {
	dataField: 'status',
	text: 'Request status',
	sort: true,
	formatter: cell => statusOptions[cell],
	filter: selectFilter({
		options: statusOptions
	})
}];

export default class RequestListing extends React.Component {

render(){
	return (
		<div>
			<h1>Requests</h1>
			<Container>
				<Row>
					<Col>User: {data.loggedInUser.id}</Col>
					<Col>Agency: {data.loggedInUser.agency}</Col>
				</Row>
			</Container>
			<BootstrapTable keyField='id' data={ data.requests } columns={ columns } pagination={ paginationFactory() } filter={ filterFactory() } />
		</div>
		);
	}
}


