/* eslint-disable import/no-named-as-default */
import {  NavLink as RRNavLink, Route, Switch } from "react-router-dom";

import AboutPage from "./AboutPage";
import HomePage from "./HomePage";
import NotFoundPage from "./NotFoundPage";
import PropTypes from "prop-types";
import React from "react";
//import { hot } from "react-hot-loader";


import {
  Collapse,
  Navbar,
  Nav,
  NavItem,
  NavbarBrand,
  NavLink,
  NavbarText
} from 'reactstrap';  

export default class App extends React.Component {

  render() {
    const activeStyle = { color: 'orange' };
    return (

      <div>
          <Navbar color="light" className={"fixed-top"} light expand="md">
            <NavbarBrand>MTLR</NavbarBrand>
            <Collapse isOpen={true} navbar>
              <Nav navbar>
                <NavItem>
                  <NavLink tag={RRNavLink} exact to="/" activeStyle={activeStyle}>Home</NavLink>
                </NavItem>
              </Nav>
              <NavbarText>v0.1.0</NavbarText>
            </Collapse>
          </Navbar>

        <Switch>
          <Route exact path="/" component={HomePage} />
          <Route path="/about" component={AboutPage} />
          <Route component={NotFoundPage} />
        </Switch>
      </div>
    );
  }
}

App.propTypes = {
  children: PropTypes.element,
  light: PropTypes.bool,
   dark: PropTypes.bool,
   fixed: PropTypes.string,
   color: PropTypes.string,
   role: PropTypes.string,
   expand: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
   tag: PropTypes.oneOfType([PropTypes.func, PropTypes.string])

};

